.App {
  text-align: center;
}

/* Adjusted or removed .App-logo since it's not used in the new App.js */

/* Keeping animations in case you reintroduce logos or similar elements */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New Navigation Styles */
.App-nav {
  background-color: #282c34;
  padding: 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Nav-link {
  color: #61dafb;
  margin: 0 10px;
  text-decoration: none;
  font-size: calc(10px + 2vmin);
}

/* Main content styling, adjusting for the new structure */
.App-main {
  padding: 20px;
  font-size: calc(10px + 2vmin);
  color: white; /* Keep text color consistent with the header */
}

section {
  margin: 20px 0;
}

/* Optional: If you reintroduce the logo or similar elements, adjust accordingly */
/*
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
*/

/* Adjusting the App-header to act as a container for nav, if needed */
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

/* If you decide to reintroduce links within the main content area */
.App-link {
  color: #61dafb;
}
